<template>
  <div>
    <headerBar :header-data="headerData" />
    <div class="container">
      <div class="common-main box">
        <van-search v-model="value" shape="round" placeholder="请输入搜索关键词" @input="serachChange()" />
        <!-- 搜索结果 begin   默认隐藏,增加active才显示 -->
        <div :class="value?'kaoqin-result active':'kaoqin-result'" @click="value=''">
          <div class="kaoqin-resultcontent" @click.stop="">
            <div v-for="(item,index) of list" :key="index">
              <div v-if="item.name.indexOf(value)!==-1 || item.mobile.indexOf(value)!==-1" class="select-renyuan" @click="goCall(item.mobile)">
                <div :class="item.xb == 2 || item.xb == '2' ? 'female':'male'" />
                <div class="name flex">
                  <span class="xm">{{ item.name }} </span>
                  <span>{{ item.mobile }}</span>
                </div>

              </div>
            </div>
            <div v-if="!serachResult">
              <div class="status">
                <div class="status-icon no-xiangmu" />
                <h3>暂无联系人</h3>
              </div>
            </div>
          </div>
        </div>
        <!-- 搜索结果 end -->
        <div class="kaoqin-renyuan">
          <van-index-bar :index-list="indexList" highlight-color="#1890ff">
            <div v-for="(item1,index1) of indexList" :key="index1">
              <div v-if="getShow(item1)">
                <van-index-anchor :index="item1" />
                <div v-for="(item,index) of list" :key="index">
                  <div v-if="item.username.charAt(0)===item1" class="select-renyuan" @click="goCall(item.mobile)">
                    <div :class="item.xb == 2 || item.xb == '2' ? 'female':'male'" />
                    <!-- <div class="female" /> -->
                    <div class="name flex">
                      <span class="xm"> {{ item.name }} </span>
                      <span>{{ item.mobile }}</span>
                    </div>

                  </div>
                </div>
              </div>
            </div>
          </van-index-bar>
        </div>
      </div>
    </div>
    <!-- 考勤类型弹窗 end -->
    <FooterNav ref="FooterNav" />
  </div>
</template>

<script land="script" scoped>
import FooterNav from '@/components/footerNav/footerNav.vue'
import { pageRouter } from '@/common/js/mixin.js'
import headerBar from '@/components/header/headerBar.vue'
export default {
  components: {
    headerBar,
    FooterNav
  },
  mixins: [pageRouter],
  data() {
    return {
      show: false, // 是否显示该字母
      serachResult: false, // 是否显示搜索暂无联系人
      value: '',
      headerData: {
        title: '通讯录',
        leftUrl: 'back',
        leftText: ' '
      },
      indexList: ['a', 'b', 'c', 'd', 'e', 'f', 'g', 'h', 'i', 'j', 'k', 'l', 'm', 'n', 'o', 'p', 'q', 'r', 's', 't', 'u', 'v', 'w', 'x', 'y', 'z'],
      list: []
    }
  },
  created() {
    this.$store.commit('router/SET_Foot_Num', 3)
    this.getList()
  },
  methods: {
    goCall(phone) {
      if (process.env.NODE_ENV !== 'development') {
        try {
          window.myapp.call(phone)
        } catch (err) {
          console.log(err)
        }
        this.$bridge.callhandler('call', phone, (data) => {
        })
      } else {
        this.$toast('返回原生页面')
      }
    },
    getList() {
      this.$store.dispatch('erp/queryContactsAction', {
        xm: ''
      }).then(res => {
        this.list = res.contacts
        for (let j = this.indexList.length - 1; j >= 0; j--) {
          let num = 0
          for (let i = 0; i < this.list.length; i++) {
            if (this.list[i].username.charAt(0) === this.indexList[j]) {
              num++
            }
          }
          if (num === 0) {
            this.indexList.splice(j, 1)
          }
        }
      })
    },
    getShow(char) {
      let num = 0
      for (let i = 0; i < this.list.length; i++) {
        if (this.list[i].username.charAt(0) === char) {
          num++
        }
      }
      if (num !== 0) {
        return true
      } else {
        return false
      }
    },
    serachChange() {
      let num = 0
      for (let i = 0; i < this.list.length; i++) {
        if (this.list[i].name.indexOf(this.value) !== -1 || this.list[i].mobile.indexOf(this.value) !== -1) {
          num++
        }
      }
      if (num === 0) {
        this.serachResult = false
      } else {
        this.serachResult = true
      }
    }
  }
}
</script>

<style scoped>
.van-index-bar{padding-top: 15px;}
.select-renyuan .name span{margin-left: 10px;}
.van-search{position: relative;z-index: 2;}
/deep/.van-index-bar__sidebar{top: calc(50% + 40px);}
</style>
